// 首页router

const Components = {
	Layer: (resolve) => require(['./pages/layer'], resolve),
	Index: (resolve) => require(['./pages/index'], resolve),
	Login: (resolve) => require(['./pages/login'], resolve),
	ComTest: (resolve) => require(['./pages/test'], resolve) // 测试模块
};

export default {
	path: '/',
	name: 'home',
	redirect: '/home',
	component: Components.Layer,
	children: [
		{
			path: 'home',
			name: 'home_index',
			component: Components.Index
		},
		{
			path: 'login',
			name: 'home_login',
			component: Components.Login
		},
		{
			path: 'test',
			name: 'home_test',
			component: Components.ComTest
		}
	]
}


