// 我的router
const Components = {
	Layer: (resolve) => require(['./pages/layer'], resolve),
	Info: (resolve) => require(['./pages/info'], resolve)
};

export default {
	path: '/my',
	name: 'my_page',
	redirect: '/my/info',
	component: Components.Layer,
	children: [
		{
			path: 'info',
			name: 'my_info',
			component: Components.Info
		}
	]
}


