export default {
  name: 'loading',
  props: {
    value: {
      type: Boolean,
      default: false
    },
    text: String,
    position: String,
    transition: {
      type: String,
      default: 'vux-mask'
    }
  },
  created () {
    this.show = this.value
  },
  data () {
    return {
      show: false
    }
  },
  watch: {
    value (val) {
      this.show = val
    },
    show (val) {
      this.$emit('input', val)
    }
  }
}



