// vuex store
import Vue from 'vue';
import Vuex from 'vuex';

import RouteModule from './global.route';
import UserModule from './global.user';

Vue.use(Vuex);

const store = new Vuex.Store({
	modules: {
		RouteState: RouteModule,
		User: UserModule
	}
});

export default store;


